import React from 'react'
import { PrinterOutlined, CloseOutlined, CloseCircleOutlined, ArrowLeftOutlined, FileDoneOutlined } from '@ant-design/icons';
import { Card, Table, Button, Popconfirm, Alert } from 'antd';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";

const { Column } = Table; 
const Invoice = (props) => { 	
	let history = useHistory();	
	const RedirectBack = () => {
		history.push(`/app/apps/sales/list`)
	}
	
	const [visible, setVisible] = React.useState(true);

	const { receiptTotal, receiptId, orderDetails, salesInfo, shopName, review, userData, sendRequest, shortTypes, getId, getDetails} = props;

	const handleClose = () => {
	  setVisible(false);
	};

	function handlePrint() {
		let container = '<table style="border-top:dashed black 2px; border-bottom:dashed black 2px; font-size:13px; width:100%; padding:0; margin:0; margin-top:5px; height:auto">';
		orderDetails.map((record) => {
		  let qty = (record.productName) ? (record.quantity + ' x ' + (record.purchasedType === '0' ? ' Pcs' : (record.purchasedType === '1' ? ' Cts' : shortTypes[parseInt(record.purchasedType)-1]))) : (record.data().quantity + ' x ' + (record.data().purchasedType === '0' ? ' Pcs' : (record.data().purchasedType === '1' ? ' Cts' : shortTypes[parseInt(record.data().purchasedType)-1])))
		  container += '<tr style="padding:0; margin:0; height:auto">';
		  container += '<td style="width:33%; max-width:35%;"><h5>'+ record.data().product +'</h5></td>';
		  container += '<td style="width:30%; max-width:30%; text-align:center;"><h5 style="text-align:center; font-weight:normal">'+ qty +'</h5></td>';
		  container += '<td style="width:35%; max-width:35%;"><h5 style="text-align:right; font-weight:normal">'+record.data().total+'<small> Rwf</small></h5></td>';
		  return container += '</tr>';
	
		});
		container += '</table>';
	
		let contained = '<table style="font-size:14px; width:100%; padding:0; margin:0; margin-top:5px; height:auto">';
		orderDetails.filter(x => x.data().purchasedType === '2').map((record) => {
		  let qty = (record.productName) ? getDetails(record) : getDetails(record.data())
		  contained += '<tr style="padding:0; margin:0; height:auto">';
		  contained += '<td style="width:40%; max-width:40%;"><h5>'+ record.data().product +'</h5></td>';
		  contained += '<td style="width:60%; max-width:60%;"><h5 style="text-align:right; font-weight:normal">'+qty+'</h5></td>';
		  return contained += '</tr>';
		});
		contained += '</table>';
		
		window.frames["print_frame"].document.body.innerHTML = `<html>
		<body>
			<div style="text-align:center">
			  <img src="/img/logo.png" height="80"/>
			  <h2 style="margin: 0; font-size:1.8em">Packing List ${getId(salesInfo._id)}<br/>
				  <small style="margin: 0; font-size:12px">${shopName.name}</small><br/><br/>
				  <span style="margin: 0; font-size:12px">Customer: ${salesInfo.customerName}</span><br/>
				  <span style="margin: 0; font-size:12px">${salesInfo.customerNumber}</span><br/>
			  </h2>
			</div>	
			${container}
			<table style="margin:0px; padding:0; width:100%;">
			<tr>
			<td style="width:59%; text-align:right; font-size:13px; font-weight:bolder;">Grand Total</td> 
			<td style="width:40%; font-size:14px; font-weight:bolder; text-align:right;">${salesInfo.amount} <small> Rwf</small></td>
			</tr>
			</table>
			${contained}
			<table style="margin:0px; padding:0; width:100%;">
			<tr>
			<td colspan="2" style="text-align:center; padding-top:10px;"><h3>Thank You!</h3></td>
			</tr>
			<tr>
			<td colspan="2" style="text-align:center; padding-bottom:10px;">Done by: ${salesInfo.backupUser}<br>${receiptId.toLocaleString()}</td>
			</tr>
			</table>
			
		</body>
	  </html> `;
		window.frames["print_frame"].window.focus();
		window.frames["print_frame"].window.print();
		
	  }

	return (
		<div className="container">
			<Card>
				<div className="d-md-flex justify-content-md-between">
					<div>
						<img	 src="/img/logo.png" alt=""/>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">{shopName.name}</span><br />
								<span>{shopName.location}</span><br />
							</p>
						</address>
					</div>
					<div className="mt-3 text-right">
						<h2 className="mb-1 font-weight-semibold">Invoice #{getId(salesInfo._id)}</h2>
						<p>{receiptId.toLocaleDateString()}</p>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">{salesInfo.customerName}</span><br />
								<span>{salesInfo.customerNumber}</span><br />
							</p>
						</address>
					</div>
				</div>
				<div className="mt-4">
					<Table dataSource={orderDetails} pagination={false} className="mb-5">
						<Column title="No." dataIndex="key" key="key" render={(_,__,index) => index+1}/>
						<Column title="Product" dataIndex="product" key="product" render={(_,record) => (record.productName) ? record.product : record.data().product}/>
						<Column title="Quantity" dataIndex="quantity" key="quantity" render={(_,record) => (record.productName) ? (record.quantity + ' x ' + (record.purchasedType === '0' ? ' Pcs' : (record.purchasedType === '1' ? ' Cts' : shortTypes[parseInt(record.purchasedType)-1]))) : (record.data().quantity + ' x ' + (record.data().purchasedType === '0' ? ' Pcs' : (record.data().purchasedType === '1' ? ' Cts' : shortTypes[parseInt(record.data().purchasedType)-1]))) }/>
						<Column title="Price" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={(record.productName) ? record.sellPrice : record.data().sellPrice} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							)} 
							key="price"
						/>
						<Column 
							title="Total" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={(record.productName) ? record.total : record.data().total} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							)} 
							key="total"
						/>
						<Column title="" dataIndex="qty" key="qty" render={(_,record) => (record.productName) ? getDetails(record) : getDetails(record.data()) }/>
					</Table>
					<div className="d-flex justify-content-end">
						<div className="text-right ">
							<div className="border-bottom">
								<p className="mb-2">
									<span>Sub - Total amount: </span>
									<NumberFormat 
										displayType={'text'} 
										value={((Math.round((receiptTotal) * 100) / 100) - (receiptTotal/ 100) * 18)} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								<p>vat (18%) : {(Math.round(((receiptTotal/ 100) * 18 ) * 100) / 100)}</p>
							</div>
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Grand Total: </span>
								<NumberFormat 
									displayType={'text'} 
									value={(Math.round((receiptTotal) * 100) / 100)} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
						</div>
					</div>
				</div>
				<div className="d-print-none">
                {(visible && salesInfo.amount > salesInfo.paid) ? (
					<Alert
						message={`Due Date: ${new Date(salesInfo.debt).toLocaleDateString()}`}
						description={salesInfo.comment}
						type="info"
						showIcon
						closable afterClose={handleClose}
					/>
					) : null}
    			</div> 
				<hr className="d-print-none"/>
				{ !review && (<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<CloseCircleOutlined /> Close
					</Button>
					<Button type="secondary" className="mr-2" onClick={handlePrint}>
						<PrinterOutlined /> Packing List
					</Button>
					<Button type="primary" className="mr-2" onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
				</div>)}
				{(review && salesInfo.refunded === false) && (<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<ArrowLeftOutlined /> Back
					</Button>
					<Button type="secondary" className="mr-2" onClick={handlePrint}>
						<PrinterOutlined /> Packing List
					</Button>
					<Button type="primary" className="mr-2" onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print Invoice</span>
					</Button>
					<Popconfirm title="Sure to request refund?" onConfirm={() => {
						sendRequest(salesInfo)
						}}>
						<Button danger>
							<CloseOutlined /> Refund
						</Button>
					</Popconfirm>
				</div>)}
				{(review && salesInfo.source === userData.userStore && salesInfo.refunded === 'Pending') && (<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<CloseCircleOutlined type="printer"/> Close
					</Button>
				</div>)}
				<iframe name="print_frame" width="0" height="0" frameborder="0" src="about:blank" title=""></iframe>
			</Card>
		</div>
	);
}

export default Invoice
